___CSS_LOADER_EXPORT___.push([module.id, `search {
  flex: 2;
  max-width: 50%;
  position: relative;
  font-size: 0.85rem;
}

.search-icon {
  width: 18px;
  height: 100%;
  position: absolute;
  left: 10px;
}

.search-icon svg {
  color: hsl(0, 0%, 70%);
}

#searchbar {
  width: 100%;
  border: hsl(205, 46%, 48%) 1px solid;
  border-radius: 5px;
  padding: 10px 13px 10px 40px;
}

#searchbar:focus {
  box-shadow: 2px 3px 3px 0 rgba(0, 0, 0, 0.1);
}

.clear-text-btn {
  height: 100%;
  width: 15px;
  background-color: transparent;
  color: var(--mid-clr);
  position: absolute;
  right: 15px;
  visibility: visible;
}

.clear-text-btn.hidden {
  visibility: hidden;
}

.results-container {
  position: absolute;
  display: none;
  background-color: white;
  width: 100%;
  border-radius: 10px;
  padding: 8px 5px;
  margin-top: 1px;
}

.results-container.results,
.results-container.no-results {
  display: block;
}

.results-container.no-results {
  padding: 25px;
}

.no-results-container {
  display: none;
}

.no-results .no-results-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
  justify-content: center;
}

.no-results .icon {
  height: 40px;
  width: 40px;
  background-color: var(--lighter-clr);
  mask-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  mask-repeat: no-repeat;
}

.no-results .error-message {
  opacity: 0.7;
}

.results ul {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.no-results ul {
  display: none;
}

.results-container button {
  padding: 10px 8px;
  background-color: transparent;
  width: 100%;
  text-align: left;
  border-radius: 5px;
}

.results-container button:hover,
.results-container button:active {
  background-color: var(--lightest-clr);
}

.results-container button:active {
  filter: brightness(0.96) saturate(1.5);
}
